<script>
  import { getContext, onMount, onDestroy } from "svelte"

  import { ONBOARDING_STEP_CHANGE_TRIGGERS } from "@/common/config/config"
  import { onboardingSteps } from "@/onboarding/config/config"

  const { currentStep, trackStepChange } = getContext(
    "deepl_onboarding_navigation"
  )

  // Initialize the bodyHeight with a default value. This value is used until the actual measurements
  // are taken. This value might be a typical expected height.
  let bodyHeight = 616

  // The height of the bottom navigation bar
  let navigationBarHeight = 41

  // This function updates the bodyHeight based on certain conditions:
  // If the document body's height is greater than the viewport height,
  // it sets the bodyHeight to the viewport height minus the navigation bar height.
  // Otherwise, it just uses the actual body height.
  // This makes sure that the navigation bar is always visibil for the user without the need of scrolling
  function updateBodyHeight() {
    if (document.body.offsetHeight > window.innerHeight) {
      bodyHeight = window.innerHeight - navigationBarHeight
    } else {
      bodyHeight = document.body.offsetHeight
    }
  }

  // When the component is mounted, this function is called to initially set the correct height
  // and to setup a resize event listener that will update the height whenever the window size changes.
  onMount(() => {
    updateBodyHeight()
    window.addEventListener("resize", updateBodyHeight)
  })

  // When the component is destroyed, remove the resize event listener to prevent memory leaks
  // and ensure the component cleans up after itself.
  onDestroy(() => {
    window.removeEventListener("resize", updateBodyHeight)
  })
</script>

<div class="dl-steps-navigation-wrapper" style={`top: ${bodyHeight}px`}>
  <div class="dl-steps-navigation" style={`height: ${navigationBarHeight}px`}>
    <div class="back-button">
      {#if $currentStep > 1}
        <a
          class="dl-btn dl-btn-ghost"
          href="#step-{$currentStep - 1}"
          on:click={() =>
            trackStepChange(
              $currentStep,
              $currentStep - 1,
              ONBOARDING_STEP_CHANGE_TRIGGERS.backButton
            )}
          data-qa="dl-onboarding-navigation-back-button"
        >
          {chrome.i18n.getMessage("onboarding_back_button")}
        </a>
      {/if}
    </div>

    <ul class="dl-navigation">
      {#each onboardingSteps as _, index}
        <li class:active={$currentStep === index + 1}>
          <a
            href="#step-{index + 1}"
            on:click={() =>
              trackStepChange(
                $currentStep,
                index + 1,
                ONBOARDING_STEP_CHANGE_TRIGGERS.navigation
              )}
            data-qa="dl-onboarding-navigation-step-{index + 1}"
            aria-label={`Step ${index + 1}`}
          >
            <span />
          </a>
        </li>
      {/each}
    </ul>

    <div class="next-button">
      {#if $currentStep < onboardingSteps.length}
        <a
          class="dl-btn dl-btn-primary"
          href="#step-{$currentStep + 1}"
          on:click={() =>
            trackStepChange(
              $currentStep,
              $currentStep + 1,
              ONBOARDING_STEP_CHANGE_TRIGGERS.nextButton
            )}
          data-qa="dl-onboarding-navigation-next-button"
        >
          {chrome.i18n.getMessage("onboarding_next_button")}
        </a>
      {/if}
    </div>
  </div>
</div>

