const ENV = {
  prod: "production",
  dev: "development",
  test: "testing",
}

export function getCurrentEnv() {
  if (env_config.IS_PROD === true) return ENV.prod
  if (env_config.IS_TEST === true) return ENV.test

  // default to DEV environment if neither PROD or TEST is specified
  return ENV.dev
}

export function isDev() {
  return getCurrentEnv() === ENV.dev
}

export function isProd() {
  return getCurrentEnv() === ENV.prod
}

export function isTest() {
  return getCurrentEnv() === ENV.test
}
