export const BROWSER_NAMES = {
  chrome: "Chrome",
  edge: "Edge",
  brave: "Brave",
  firefox: "Firefox",
  opera: "Opera",
  safari: "Safari",
}
export function isMac() {
  const platform =
    navigator?.userAgentData?.platform || navigator?.platform || undefined
  return platform && platform?.toUpperCase().indexOf("MAC") !== -1
}

export function isDarkMode() {
  if (window.matchMedia) {
    return window.matchMedia("(prefers-color-scheme: dark)").matches
  }

  return false
}

function isChromiumBrowser(browser) {
  if (
    browser === BROWSER_NAMES.chrome ||
    browser === BROWSER_NAMES.edge ||
    browser === BROWSER_NAMES.brave ||
    browser === BROWSER_NAMES.opera
  ) {
    return true
  }

  return false
}

function getBrowserVersion(browser, userAgent) {
  let version

  try {
    switch (browser) {
      case BROWSER_NAMES.chrome:
      case BROWSER_NAMES.brave:
        version = userAgent.split("Chrome/")[1].split(" ")[0]
        break
      case BROWSER_NAMES.edge:
        version = userAgent.split("Edg/")[1].split(" ")[0]
        break
      case BROWSER_NAMES.firefox:
        version = userAgent.split("Firefox/")[1].split(" ")[0]
        break
      case BROWSER_NAMES.opera:
        version = userAgent.split("OPR/")[1].split(" ")[0]
        break
      case BROWSER_NAMES.safari:
        version = userAgent.split("Version/")[1].split(" ")[0]
        break
      default:
        break
    }
  } catch (error) {
    console.error(
      `Could not get the browsers (${browser}) version from user agent`,
      error
    )
  }

  // For chromium based browers we can use the navigator.userAgentData API
  try {
    if (!version && navigator?.userAgentData && isChromiumBrowser(browser)) {
      version = getChromiumBrand(browser).version
    }
  } catch (error) {
    console.error(
      `Could not get the chromium browsers (${browser}) version`,
      error
    )
  }

  if (version && typeof version === "string") {
    return version
  }

  return "unknown_browser_version"
}

function getChromiumBrand(browser) {
  // Browser is not Chromium based
  if (!navigator.userAgentData || !browser) return

  const regex = new RegExp(`${browser}`, "i")
  return navigator.userAgentData.brands.filter((b) => regex.test(b.brand))[0]
}

// Get meta data about the browser and operating system (platform)
export function getPlatformMetaData() {
  const userAgent = navigator?.userAgent || "unknown_user_agent"
  const platform =
    navigator?.userAgentData?.platform ||
    navigator?.platform ||
    "unknown_platform"

  // userAgent sniffing is not a reliable way to get the browser name and version
  // if we track the browser name and version and we detect we have a lot of "unknown" values
  // we should adapt this logic or use a proper lib for that
  let browser = "unknown_browser"
  let browserVersion = "unknown_browser_version"
  const browserLang = navigator?.language || "unknown_browser_language"

  try {
    const isChrome = userAgent.match(/chrome|chromium|crios/i)
    const isEdge =
      userAgent.match(/edg/i) || getChromiumBrand(BROWSER_NAMES.edge)
    const isFirefox = userAgent.match(/firefox|fxios/i)
    const isOpera =
      userAgent.match(/opr\//i) || getChromiumBrand(BROWSER_NAMES.opera)
    const isSafari = userAgent.match(/safari/i)
    const isBrave = getChromiumBrand(BROWSER_NAMES.brave)

    if (isChrome) {
      browser = BROWSER_NAMES.chrome
      // Since some browsers are based on Chromium, the user agent could include Chrome depending on the version.
      // We could also change the order of the surrounding if statement, but that would make it harder to understand
      if (isEdge) {
        browser = BROWSER_NAMES.edge
      }
      if (isOpera) {
        browser = BROWSER_NAMES.opera
      }
      if (isBrave) {
        browser = BROWSER_NAMES.brave
      }
    } else if (isEdge) {
      browser = BROWSER_NAMES.edge
    } else if (isFirefox) {
      browser = BROWSER_NAMES.firefox
    } else if (isOpera) {
      browser = BROWSER_NAMES.opera
    } else if (isSafari) {
      browser = BROWSER_NAMES.safari
    }

    browserVersion = getBrowserVersion(browser, userAgent)
  } catch (error) {
    console.error("Could not get the name of the brower", error)
  }

  return {
    userAgent,
    platform,
    browser,
    browserVersion,
    browserLang,
  }
}

// Checks if the extension is running in the firefox browser
export function isFirefoxExtension() {
  if (env_config.browserBuild) {
    return (
      env_config.browserBuild.toLowerCase() ===
      BROWSER_NAMES.firefox.toLowerCase()
    )
  }

  const extensionURL = chrome.runtime.getURL("")

  // the getPlatformMetaData mehod is based on user-agent sniffing. This is not a very reliable methode to detect the browser
  // Therefore we also check if the extension url matches the url of a firefox extension
  return (
    getPlatformMetaData().browser === BROWSER_NAMES.firefox ||
    /moz-extension:\/\//gm.test(extensionURL)
  )
}

// For certain browser we can not use a native extension api for tts
// Therefore we use the WebSpeechAPI
// Here we get all supported voices
export function getWebSpeechAPIVoices() {
  return new Promise((resolve, reject) => {
    if ("speechSynthesis" in window) {
      const voices = speechSynthesis.getVoices()

      if (voices.length > 0) {
        resolve(voices)
      } else {
        speechSynthesis.onvoiceschanged = () => {
          const updatedVoices = speechSynthesis.getVoices()
          resolve(updatedVoices)
        }
      }
    } else {
      reject(new Error("SpeechSynthesis not supported"))
    }
  })
}

// For certain browser we can not use a native extension api for tts
// Therefore we use the WebSpeechAPI
// Here we start tts
export function speakTextWithWebSpeechAPI(text, lang, onEnd) {
  const utterance = new SpeechSynthesisUtterance(text)
  utterance.lang = lang

  utterance.onend = function () {
    onEnd()
  }

  speechSynthesis.speak(utterance)
}

export const shouldDisplayOfficeBanner = () => {
  const regex = /(outlook\.office\.com|sharepoint\.com|microsoft|office)/
  const url = new URL(window.location.href)

  if (!regex.test(url.origin)) return false

  const match = url.origin.match(regex)

  return match ? match[0] : false
}
