import * as Sentry from "@sentry/browser"
import { getCurrentEnv, isProd, isDev } from "@/common/utils/app"
import { settings } from "@/common/stores"
import { userHasProServiceLevel } from "@/common/utils/userState"
import { SERVICE_NAME } from "@/common/config/config"

export const getIgnoredErrorPatterns = () => {
  return [
    /Receiving end does not exist/,
    /message channel closed before a response was received/,
    /Extension context invalidated/,
  ]
}

/**
 * Determines whether an error message should be ignored based on predefined regex patterns.
 *
 * This function checks if the given `errorMessage` matches any of the specified regex patterns
 * that correspond to known, non-critical errors. If the error matches, the function returns `true`,
 * indicating that the error should be ignored (i.e., not sent to Sentry).
 *
 * @param {string} errorMessage - The error message to evaluate against the ignore patterns.
 * @returns {boolean} - Returns `true` if the error message matches one of the ignore patterns,
 *                      otherwise returns `false`.
 *
 * @example
 * const errorMessage = '(in promise) Error: Could not establish connection. Receiving end does not exist.';
 * const shouldIgnore = shouldIgnoreError(errorMessage);
 * console.log(shouldIgnore); // true if the message matches any ignore pattern
 */
export const shouldIgnoreError = (errorMessage) => {
  const ignorePatterns = getIgnoredErrorPatterns()

  // If the message matches any of the ignore patterns, drop the event
  if (ignorePatterns.some((pattern) => pattern.test(errorMessage))) {
    return true // Returning null drops the event
  }

  return false
}

/**
 * Initializes global error handlers to suppress specific ignored errors in the content script.
 *
 * This function sets up listeners for uncaught errors and unhandled promise rejections
 * in the content script. If the error or rejection matches predefined patterns (as determined
 * by the `shouldIgnoreError` function), the default browser behavior is prevented, and the error
 * is not logged to the console. For other errors, the default behavior is preserved.
 *
 * @function suppressIgnoredErrors
 *
 * @example
 * suppressIgnoredErrors(); // Sets up global error suppression for specific error patterns
 */
export const suppressIgnoredErrors = () => {
  // Devs should see all errors
  if (isDev()) return

  /**
   * Global error handler for the content script.
   * This listener captures any uncaught errors that occur in the content script.
   * It checks the error message against predefined patterns using the `shouldIgnoreError` function.
   * If the error matches a pattern, it prevents the default behavior (logging the error to the console).
   *
   * @event error
   * @param {ErrorEvent} event - The error event object that contains details about the uncaught error.
   */
  window.addEventListener("error", function (event) {
    // Extract the error message, or use an empty string if not available
    const errorMessage =
      event.message || (event.error && event.error.message) || ""

    // Check if the error message should be ignored based on regex patterns
    if (shouldIgnoreError(errorMessage)) {
      // Prevent the default behavior (e.g., logging to the console)
      event.preventDefault() // Prevent error from being logged or propagated
    }
  })

  /**
   * Global unhandled promise rejection handler for the content script.
   * This listener captures any unhandled promise rejections that occur in asynchronous code within the content script.
   * It checks the rejection reason against predefined patterns using the `shouldIgnoreError` function.
   * If the rejection matches a pattern, it prevents the default behavior (logging the rejection to the console).
   *
   * @event unhandledrejection
   * @param {PromiseRejectionEvent} event - The event object containing details of the unhandled promise rejection.
   */
  window.addEventListener("unhandledrejection", function (event) {
    // Extract the rejection reason message, or use an empty string if not available
    const errorMessage = (event.reason && event.reason.message) || ""

    // Check if the rejection reason should be ignored based on regex patterns
    if (shouldIgnoreError(errorMessage)) {
      // Prevent the default behavior (e.g., logging to the console)
      event.preventDefault() // Stop promise rejection from being logged
    }
  })
}

export const initialSentryConfig = {
  dsn: "https://f7c23f019d154c319e91e8de004727dc@errortracking.deepl.com/12",
  release: "deepl-browser-extension@" + env_config.APP_VERSION,
  environment: getCurrentEnv(),
  // Sets the traces sample rate for performance monitoring.
  // In production, the rate is 0.1 (10% of transactions are sampled).
  // In other environments, the rate is 1.0 (100% of transactions are sampled).
  tracesSampleRate: isProd() ? 0.1 : 1.0,
  // Sets the sample rate for performance monitoring.
  // In production, the rate is 0.1 (10% of all actions are sampled).
  // In other environments, the rate is 1.0 (100% of transactions are sampled).
  sampleRate: isProd() ? 0.1 : 1.0,
  // Array of regex patterns that define errors to be ignored by Sentry
  ignoreErrors: getIgnoredErrorPatterns(),
  beforeSend(event) {
    const message =
      event.message ||
      (event.exception &&
        event.exception.values &&
        event.exception.values[0].value) ||
      ""

    /**
     * We use both `ignoreErrors` (from the Sentry configuration) and the `shouldIgnoreError`
     * function in `beforeSend` because during testing, it was found that certain errors were
     * successfully ignored by one method but not the other. Using both ensures more comprehensive
     * error filtering.
     */
    if (shouldIgnoreError(message)) {
      return null // Returning null drops the event, preventing it from being sent to Sentry
    }

    // If it doesn't match, allow the event to be sent to Sentry
    return event
  },
}

export function initErrorTracking() {
  suppressIgnoredErrors()

  Sentry.init({ ...initialSentryConfig })

  createSentryUserListener(Sentry)

  return Sentry
}

export const createSentryUserListener = (scope) => {
  chrome.storage.sync.get(
    [
      "installationId",
      "session",
      "isLoggedIn",
      "translatorServiceType",
      "writeServiceType",
    ],
    function (settings) {
      scope.setTag("internal_session_id", settings.session.id)
      scope.setTag(
        "is_translator_pro",
        userHasProServiceLevel(settings, SERVICE_NAME.TRANSLATOR)
      )
      scope.setTag(
        "is_write_pro",
        userHasProServiceLevel(settings, SERVICE_NAME.WRITE)
      )
      scope.setTag("is_logged_in", settings.isLoggedIn)
    }
  )

  chrome.storage.local.get("browserInstanceId", function (settings) {
    scope.setTag("browser_instance_id", settings.browserInstanceId)
  })

  chrome.storage.onChanged.addListener((changes) => {
    if (changes?.session?.newValue) {
      scope.setTag("internal_session_id", changes.session.newValue.id)
    }
    if (changes?.installationId) {
      scope.setUser({ id: changes.installationId })
    }
    if (changes?.browser_instance_id) {
      scope.setTag("browser_instance_id", settings.browserInstanceId.newValue)
    }
    if (
      changes.isLoggedIn?.newValue === true ||
      changes.isLoggedIn?.newValue === false
    ) {
      scope.setTag("is_logged_in", changes.isLoggedIn.newValue)
    }
    if (changes.translatorServiceType) {
      chrome.storage.sync.get(
        ["isLoggedIn"],
        scope.setTag(
          "is_translator_pro",
          userHasProServiceLevel(
            {
              ...settings,
              translatorServiceType: changes.translatorServiceType.newValue,
            },
            SERVICE_NAME.TRANSLATOR
          )
        )
      )
    }
    if (changes.writeServiceType) {
      chrome.storage.sync.get(
        ["isLoggedIn"],
        scope.setTag(
          "is_write_pro",
          userHasProServiceLevel(
            {
              ...settings,
              writeServiceType: changes.writeServiceType.newValue,
            },
            SERVICE_NAME.WRITE
          )
        )
      )
    }

    return true
  })
}

// @todo - pass in the complete error instead of error.message
export function trackErrorMessage(message, scope) {
  scope.captureException(message)
}

