export function isIncludedInTheText(referenceText, text) {
  if (!referenceText || !text) {
    return false
  }

  const regEx = /[\n\r\s]+/gm

  const simplifiedReferenceText = referenceText.replace(regEx, "")
  const simplifiedText = text.replace(regEx, "")

  return simplifiedReferenceText.includes(simplifiedText)
}

function findNextBlockPosition(string, blocks, start) {
  let position = {
    block: "end",
    position: string.length,
  }

  blocks.forEach((block) => {
    const pos = string.indexOf(`<${block}>`, start)
    if (pos > -1 && pos < position.position) {
      position = {
        block: block,
        position: pos,
      }
    }
  })

  return [position.block, position.position]
}

export function getBlocksFromString(string, blocks = []) {
  const parts = []

  let lastPosition = 0

  let isEnd = false

  while (!isEnd) {
    const [block, currentPosition] = findNextBlockPosition(
      string,
      blocks,
      lastPosition
    )
    if (block === "end") {
      isEnd = true
    }

    if (lastPosition < currentPosition) {
      parts.push({
        block: "main",
        text: string.substring(lastPosition, currentPosition),
      })
    }

    if (isEnd) {
      break
    }

    const endPosition = string.indexOf(`</${block}>`, currentPosition)
    if (endPosition === -1) {
      parts.push({
        block,
        text: string.substring(currentPosition + `<${block}>`.length),
      })
      isEnd = true
    } else {
      parts.push({
        block,
        text: string.substring(
          currentPosition + `<${block}>`.length,
          endPosition
        ),
      })

      lastPosition = endPosition + `</${block}>`.length
    }
  }

  return parts
}

function splitTextIntoTokens(text) {
  return text.match(/[^\s.,:;'"]+|[.,:;'"]|\s+/g) || []
}

/**
 * Todos:
 *  - Don't use start_token and don't slice array
 *  - Add backward search
 *
 * @param Array tokens1
 * @param Array tokens2
 * @return [ NumberOfMatchingTokens, [NumberOfTokensFromTokens1, NumberOfTokensFromTokens2], NumberOfMatchingTokens, ...  ]
 * @see E. Myers: An O(ND) Difference Algorithm and Its Variations, http://www.xmailserver.org/diff2.pdf
 */
function diff(tokens1, tokens2, _isEqual) {
  const TAG = "[diff]"
  const isEqual = _isEqual || ((a, b) => a === b)

  // determine common prefix
  const prefixLength = (() => {
    let i = 0
    while (isEqual(tokens1[i], tokens2[i]) && i < tokens1.length) ++i
    return i
  })()

  if (prefixLength === tokens1.length && tokens1.length === tokens2.length) {
    return [prefixLength]
  }

  // determine common postfix
  const postfixLength = (() => {
    let i1 = tokens1.length - 1
    let i2 = tokens2.length - 1
    while (
      isEqual(tokens1[i1], tokens2[i2]) &&
      i1 > prefixLength &&
      i2 > prefixLength
    ) {
      --i1
      --i2
    }
    return tokens1.length - i1 - 1
  })()

  const maxX = tokens1.length - prefixLength - postfixLength + 1
  const maxY = tokens2.length - prefixLength - postfixLength + 1

  const maxDist = maxX + maxY
  const kLookup = new Map()
  const paths = new Map()
  kLookup.set(1, 0)
  paths.set(1, null)

  for (let dist = 0; dist <= maxDist; ++dist) {
    // console.log(TAG, "D:", D);
    for (let k = -dist; k <= dist; k += 2) {
      let x, y, path
      if (
        k == -dist ||
        (k != dist && kLookup.get(k - 1) < kLookup.get(k + 1))
      ) {
        x = kLookup.get(k + 1)
        path = [paths.get(k + 1), undefined, undefined]
      } else {
        x = kLookup.get(k - 1) + 1
        path = [paths.get(k - 1), undefined, undefined]
      }
      y = x - k
      while (
        x < maxX &&
        y < maxY &&
        isEqual(tokens1[x + prefixLength], tokens2[y + prefixLength])
      ) {
        // match
        ++x
        ++y
      }
      if (x >= maxX && y >= maxY) {
        // target reached -> create diff from shortest path
        path[1] = maxX + postfixLength - 1
        path[2] = maxY + postfixLength - 1
        //console.log(TAG, "length is ", D,"\n ---------- ");
        //console.log(TAG, "a:", a);
        //console.log(TAG, "b:", b);
        //console.log(TAG, "path:", path);
        let x = -1,
          y = -1
        const collectedPathReversed = []
        while (path) {
          collectedPathReversed.push([path[1], path[2]])
          path = path[0]
        }
        // console.log(TAG, "collectedPathReversed:", Array.from(collectedPathReversed));

        let steps = [],
          currentAlterationStep = null
        while (collectedPathReversed.length) {
          const next = collectedPathReversed.pop()
          const xNext = next[0],
            yNext = next[1]
          const dx = xNext - x,
            dy = yNext - y
          const d = Math.min(dx, dy)
          if (dx > d) {
            if (currentAlterationStep) {
              // && currentAlterationStep[0] === 0){
              currentAlterationStep[0] += dx - d
            } else {
              currentAlterationStep = [dx - d, 0]
              steps.push(currentAlterationStep)
            }
          }
          if (dy > d) {
            if (currentAlterationStep) {
              // && currentAlterationStep[1] === 0){
              currentAlterationStep[1] += dy - d
            } else {
              currentAlterationStep = [0, dy - d]
              steps.push(currentAlterationStep)
            }
          }
          if (d > 0) {
            steps.push(d)
            currentAlterationStep = null
          }
          x = xNext
          y = yNext
        }
        //console.log(steps);
        steps[0] = steps[0] - 1 + prefixLength // remove start token
        return steps
      }

      kLookup.set(k, x)
      path[1] = x
      path[2] = y
      paths.set(k, path)
    }
  }
  throw new Error(TAG, "should not happen?")
}

function createTokenDiff(firstSentence, secondSentence) {
  // calculate the token diff based on the diff of the tokensWithPrefixSpaces
  // Note: The new diff might introduce duplicated whitespaces which have to be dealt with after applying the diff.
  // XXX: Claudius needs to specify why this is needed as opposed to just using diff(this.originalTokens, this.correctedTokens)
  const t1 = splitTextIntoTokens(firstSentence) // e.g.: This|,| is| a| test.
  const t2 = splitTextIntoTokens(secondSentence) // e.g.:This|,| was| not| a| test.
  const d = diff(t1, t2) // e.g.: [2, [1,2], 2]
  const d_separatedSpaces = [] // e.g.: -> [3, [1,3], 4]
  let i_d = 0
  let i_t1 = 0
  let i_t2 = 0

  // console.log(TAG, "[" + t1.join("|") + "]", "[" + t2.join("|") + "]");
  while (i_d < d.length) {
    // matchLength is the number of tokens that are identical in both token collections
    const matchLength = d[i_d]
    // matchLength_separated is
    let matchLength_separated = 0

    for (let i = i_t1 + matchLength - 1; i >= i_t1; i -= 1) {
      // for (let i = i_t1; i < i_t1 + matchingLength; ++i){
      matchLength_separated += t1[i]?.match(/^\s/) ? 2 : 1
    }
    i_t1 += matchLength
    i_t2 += matchLength
    let diffLength1_separated = 0
    let diffLength2_separated = 0
    // extend match to the following whitespace
    if (
      i_t1 < t1.length &&
      i_t2 < t2.length &&
      t1[i_t1]?.match(/^\s/) &&
      t2[i_t2]?.match(/^\s/)
    ) {
      diffLength1_separated -= 1
      diffLength2_separated -= 1
      matchLength_separated += 1
    }
    d_separatedSpaces.push(matchLength_separated)
    i_d += 1

    if (i_d < d.length) {
      // diff
      const [diffLength1, diffLength2] = d[i_d]
      for (let i = i_t1 + diffLength1 - 1; i >= i_t1; i -= 1) {
        // count t1 tokens beginning with spaces
        diffLength1_separated += t1[i]?.match(/^\s/) ? 2 : 1
      }
      for (let i = i_t2 + diffLength2 - 1; i >= i_t2; i -= 1) {
        // count t2 tokens beginning with spaces
        diffLength2_separated += t2[i]?.match(/^\s/) ? 2 : 1
      }
      d_separatedSpaces.push([
        Math.max(diffLength1_separated, 0),
        Math.max(diffLength2_separated, 0),
      ])
      i_d += 1
      i_t1 += diffLength1
      i_t2 += diffLength2
    }
  }
  return [d_separatedSpaces, t1, t2, d]
}

export function createHighlightsForSentence(
  targetSentenceText,
  sourceSentenceText
) {
  const tokenDiff = createTokenDiff(sourceSentenceText, targetSentenceText)
  const highlights = []

  let i1 = 0
  let i2 = 0
  let currentOffset = 0
  let originalOffset = 0
  tokenDiff[3].forEach((diffEntry) => {
    if (
      Array.isArray(diffEntry) &&
      typeof diffEntry[0] === "number" &&
      typeof diffEntry[1] === "number"
    ) {
      if (diffEntry[1] !== 0) {
        const change = tokenDiff[2].slice(i2, i2 + diffEntry[1]).join("")
        const orig = tokenDiff[1].slice(i1, i1 + diffEntry[0]).join("")

        const highlight = {
          original: orig,
          replace: change,
        }

        highlight.changeLength = change.length
        highlight.changeOffset = currentOffset

        highlight.originalLength = orig.length
        highlight.originalOffset = originalOffset

        currentOffset += change.length
        originalOffset += orig.length
        highlights.push(highlight)
      }
      i1 += diffEntry[0]
      i2 += diffEntry[1]
    } else {
      let length = 0
      tokenDiff[2]
        .slice(i2, i2 + diffEntry)
        .forEach((w) => (length += w.length))

      currentOffset += length

      let originalLength = 0
      tokenDiff[1]
        .slice(i1, i1 + diffEntry)
        .forEach((w) => (originalLength += w.length))

      originalOffset += originalLength

      i1 += diffEntry
      i2 += diffEntry
    }
  })
  return highlights
}

export function getDiffChunks(targetSentenceText, sourceSentenceText) {
  let lastIndex = 0
  const chunkData = createHighlightsForSentence(
    targetSentenceText,
    sourceSentenceText
  )
  const textChunks = []

  for (let chunkItem of chunkData) {
    if (chunkItem.changeOffset > lastIndex) {
      textChunks.push({
        isHighlighted: false,
        text: targetSentenceText.substring(lastIndex, chunkItem.changeOffset),
      })
    }

    const endOffset = chunkItem.changeOffset + chunkItem.changeLength

    textChunks.push({
      isHighlighted: true,
      text: targetSentenceText.substring(chunkItem.changeOffset, endOffset),
    })

    lastIndex = endOffset
  }

  if (lastIndex < targetSentenceText.length) {
    textChunks.push({
      isHighlighted: false,
      text: targetSentenceText.substring(lastIndex, targetSentenceText.length),
    })
  }

  return textChunks
}

/**
 * Retrieves all the text of a DOM node, but skips any elements with display:none
 */
export const getTextContentWithoutHiddenElements = (element) => {
  let textContent = ""
  for (const node of element.childNodes) {
    if (node.nodeType === Node.TEXT_NODE) {
      textContent += node.textContent
    } else if (
      node.nodeType === Node.ELEMENT_NODE &&
      node.style.display !== "none"
    ) {
      textContent += getTextContentWithoutHiddenElements(node)
    }
  }
  return textContent
}
