import { isFirefoxExtension } from "@/common/utils/platform"

export const INTERNAL_PAGE_KEYS = {
  developer_tools: "DEVELOPER_TOOLS",
  settings_page: "SETTINGS_PAGE",
  settings_writing: "SETTINGS_WRITING",
  settings_reading: "SETTINGS_READING",
  settings_customizations: "SETTINGS_CUSTOMIZATIONS",
  settings_glossary: "SETTINGS_GLOSSARY",
  onboarding: "ONBOARDING",
  onboarding_after_successful_login: "ONBOARDING_AFTER_SUCCESSFUL_LOGIN",
  firefox_first_run: "FIREFOX_FIRST_RUN",
  privacy_policy: "PRIVACY_POLICY",
  create_account: "CREATE_ACCOUNT",
}

const INTERNAL_PAGE_FILES = {
  DEVELOPER_TOOLS: "dev-page.html",
  SETTINGS_PAGE: "settings.html",
  SETTINGS_READING: "settings.html#translator",
  SETTINGS_WRITING: "settings.html#write",
  SETTINGS_CUSTOMIZATIONS: "settings.html#customizations",
  SETTINGS_GLOSSARY: "settings.html#glossary",
  ONBOARDING: "onboarding.html",
  ONBOARDING_FIREFOX: "onboarding-firefox.html",
  ONBOARDING_AFTER_SUCCESSFUL_LOGIN: "onboarding.html#successful_login",
  FIREFOX_FIRST_RUN: "firefox-first-run.html",
  PRIVACY_POLICY: "privacy-policy.html",
  CREATE_ACCOUNT: "create-account.html",
}

export function openInternalPage({ payload }) {
  const { destination, openInSameTab } = payload

  if (destination === undefined) return

  let redirectURL = ""

  switch (destination) {
    case INTERNAL_PAGE_KEYS.developer_tools:
      redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.DEVELOPER_TOOLS)
      break
    case INTERNAL_PAGE_KEYS.settings_page:
      redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.SETTINGS_PAGE)
      break
    case INTERNAL_PAGE_KEYS.settings_writing:
      redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.SETTINGS_WRITING)
      break
    case INTERNAL_PAGE_KEYS.settings_reading:
      redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.SETTINGS_READING)
      break
    case INTERNAL_PAGE_KEYS.settings_customizations:
      redirectURL = buildInternalUrl(
        INTERNAL_PAGE_FILES.SETTINGS_CUSTOMIZATIONS
      )
      break
    case INTERNAL_PAGE_KEYS.settings_glossary:
      redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.SETTINGS_GLOSSARY)
      break
    case INTERNAL_PAGE_KEYS.onboarding:
      if (isFirefoxExtension()) {
        redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.ONBOARDING_FIREFOX)
      } else {
        redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.ONBOARDING)
      }
      break
    case INTERNAL_PAGE_KEYS.onboarding_after_successful_login:
      redirectURL = buildInternalUrl(
        INTERNAL_PAGE_FILES.ONBOARDING_AFTER_SUCCESSFUL_LOGIN
      )
      break
    case INTERNAL_PAGE_KEYS.firefox_first_run:
      redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.FIREFOX_FIRST_RUN)
      break
    case INTERNAL_PAGE_KEYS.privacy_policy:
      redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.PRIVACY_POLICY)
      break
    case INTERNAL_PAGE_KEYS.create_account:
      redirectURL = buildInternalUrl(INTERNAL_PAGE_FILES.CREATE_ACCOUNT)
      break
    default:
      break
  }

  if (!redirectURL) return

  if (openInSameTab) {
    chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
      const currentTab = tabs[0]
      chrome.tabs.update(currentTab.id, { url: redirectURL })
    })
  } else {
    chrome.tabs.create({
      url: redirectURL,
      active: true,
    })
  }
}

function buildInternalUrl(file) {
  return chrome.runtime.getURL(file)
}
