<script>
  import { onMount } from "svelte"
  import { settings } from "@/common/stores"
  import { sendAction } from "@/common/utils/messaging"

  import { INTERNAL_PAGE_KEYS } from "@/background/internalPages"
  import { URL_ACTIONS } from "@/background/redirect"
  import { UI_LOCATIONS } from "@/common/config/config"
  import { getBlocksFromString } from "@/common/utils/textProcessing"

  import Layout from "@/onboarding/components/layout/Layout.svelte"

  $: {
    // Since this screen only shows up on first install, all users should start logged out.
    // If this value changes we can assume log in worked and redirect to start of normal onboarding
    // `document.hidden` will be false if this tab is unfocused and the user logs in elsewhere,
    // but true if they are still on this tab, which is the UX we want
    if ($settings.isLoggedIn && !document.hidden) {
      sendAction({
        action: "dlOpenInternalPage",
        payload: {
          destination: INTERNAL_PAGE_KEYS.onboarding_after_successful_login,
          openInSameTab: true,
        },
      })
    }
  }

  function redirectToCreateAccountPage() {
    sendAction({
      action: "dlExternalURLRedirect",
      payload: {
        destination: URL_ACTIONS.deepl_create_account,
        openInSameTab: true,
        utmContent: "onboarding_signup_step",
        redirectToOnboarding: true,
      },
    })

    sendAction({
      action: "dlTrackUILocationClicked",
      payload: UI_LOCATIONS.onboardingSignUp,
    })
  }

  function skipAccountCreation() {
    sendAction({
      action: "dlOpenInternalPage",
      payload: {
        destination: INTERNAL_PAGE_KEYS.onboarding,
        openInSameTab: true,
      },
    })

    sendAction({
      action: "dlTrackUILocationClicked",
      payload: UI_LOCATIONS.onboardingSkipSignUp,
    })
  }

  function handleLogin() {
    sendAction({
      action: "dlRequestLogin",
    })

    sendAction({
      action: "dlTrackUILocationClicked",
      payload: UI_LOCATIONS.onboardingLoginLink,
    })
  }

  onMount(() => {
    $settings.hasUserSeenAccountCreationNudge = true
  })
</script>

<Layout showNavigation={false} showPinMessage={false}>
  <div class="dl-onboarding-create-account">
    <div class="title">
      {chrome.i18n.getMessage("onboarding_signup_header")}
    </div>
    <div class="content">
      {chrome.i18n.getMessage("onboarding_signup_body")}
    </div>
    <button on:click={redirectToCreateAccountPage} class="create-account">
      {chrome.i18n.getMessage("onboarding_signup_cta")}
    </button>
    <div>
      {#each getBlocksFromString( chrome.i18n.getMessage("onboarding_signup_login_option"), ["loginLink"] ) as part}
        {#if part.block === "loginLink"}
          <span class="login-link" on:click={handleLogin}
            >{@html part.text}</span
          >
        {:else}
          <span>
            {@html part.text}
          </span>
        {/if}
      {/each}
    </div>
    <button class="skip-button" on:click={skipAccountCreation}
      >{chrome.i18n.getMessage("onboarding_signup_skip_option")}</button
    >
  </div>
</Layout>

<style lang="scss">
  @import "~dl-ui-library/variables.scss";

  :global(body) {
    // to override the global colored background from Layout
    background-color: white !important;
  }

  .dl-onboarding-create-account {
    box-sizing: border-box;
    border-radius: $border_radius_sm;
    padding-top: 180px;
    max-width: 440px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  .title {
    font-size: 32px;
    font-weight: 400;
  }

  .content {
    font-size: 16px;
    margin-bottom: 30px;
  }

  .create-account {
    background-color: #0177a9;
    border-radius: 6px;
    border: none;
    color: #fff;
    cursor: pointer;
    height: 44px;
    padding: 11px 24px 13px 24px;
    width: 100%;
  }

  .login-link {
    color: #006494;
    cursor: pointer;
  }

  .skip-button {
    align-self: end;
    background-color: white;
    border-radius: 6px;
    border: 1px solid #dbe0e5;
    color: black;
    cursor: pointer;
    font-weight: 600;
    height: 44px;
    padding: 11px 24px 13px 24px;
  }
</style>
