import { SERVICE_LEVEL, SERVICE_NAME } from "@/common/config/config"

export const NO_SETTINGS_ERROR = "No settings supplied to getServiceType"

export const UNIDENTIFIED_SERVICE_ERROR_MESSAGE =
  "Unidentified service name supplied to getServiceType"

/** @typedef {keyof typeof import("@/common/config/config").SERVICE_NAME} ServiceName */

/** @param {ServiceName} serviceName */
export const getServiceType = (settings, serviceName) => {
  if (!settings) {
    throw new Error(NO_SETTINGS_ERROR)
  }

  if (!Object.keys(SERVICE_NAME).includes(serviceName)) {
    throw new Error(UNIDENTIFIED_SERVICE_ERROR_MESSAGE)
  }

  // Not logged in = free
  if (!settings.isLoggedIn) {
    return SERVICE_LEVEL.free
  }

  // User is logged in
  switch (serviceName) {
    case SERVICE_NAME.TRANSLATOR:
      return settings.translatorServiceType
    case SERVICE_NAME.WRITE:
      return settings.writeServiceType
    case SERVICE_NAME.API:
      return settings.apiServiceType
    case SERVICE_NAME.DOC_TRANSLATOR:
      return settings.docTranslatorServiceType
  }
}

/** @param {ServiceName} serviceName */
export const userHasProServiceLevel = (settings, serviceName) => {
  return getServiceType(settings, serviceName) === SERVICE_LEVEL.pro
}

/** @param {ServiceName} serviceName */
export const userHasFreeServiceLevel = (settings, serviceName) => {
  return getServiceType(settings, serviceName) === SERVICE_LEVEL.free
}

/** @param {ServiceName} serviceName */
export const userHasNoneServiceLevel = (settings, serviceName) => {
  return getServiceType(settings, serviceName) === SERVICE_LEVEL.none
}

export const userHasAnyProSubscription = (settings) => {
  if (
    userHasProServiceLevel(settings, SERVICE_NAME.TRANSLATOR) ||
    userHasProServiceLevel(settings, SERVICE_NAME.WRITE)
  ) {
    return true
  }

  return false
}

export const isWriteFreeAccount = (settings) => {
  if (!settings.isLoggedIn) {
    return false
  }

  return userHasFreeServiceLevel(settings, SERVICE_NAME.WRITE)
}

export const userHasGlossaryService = (settings) => {
  return settings.isLoggedIn && settings.hasTermbaseService
}

export const userIsLoggedInAndHasFreeServiceLevel = (settings, serviceName) => {
  return settings.isLoggedIn && userHasFreeServiceLevel(settings, serviceName)
}
