export function getExternalUrl(data) {
  const { destination, ...additionalData } = data

  if (destination === undefined) return

  switch (destination) {
    case URL_ACTIONS.share_feedback_survey:
      return URL_LIST.DOCS_GOOGLE_COM_FEEDBACK_FORM
    case URL_ACTIONS.new_share_feedback_survey:
      return URL_LIST.NEW_FEEDBACK_FORM
    case URL_ACTIONS.try_DeepL_Pro:
      if (additionalData?.utmContent) {
        return addParamsToURL(URL_LIST.DEEPL_COM_PRO, [
          ["utm_source", "browser_extension"],
          ["utm_medium", "chrome"],
          ["utm_content", additionalData?.utmContent],
        ])
      } else {
        return URL_LIST.DEEPL_COM_PRO
      }
    case URL_ACTIONS.try_DeepL_Pro_Write:
      if (additionalData?.utmContent) {
        return addParamsToURL(URL_LIST.DEEPL_COM_PRO_WRITE, [
          ["utm_source", "browser_extension"],
          ["utm_medium", "chrome"],
          ["utm_content", additionalData?.utmContent],
        ])
      } else {
        return URL_LIST.DEEPL_COM_PRO_WRITE
      }
    case URL_ACTIONS.show_more_translation:
      if (additionalData) {
        return getShareURL(additionalData)
      } else {
        console.error("Invalid action.")
        return
      }
    case URL_ACTIONS.deepl_translator_home:
      if (additionalData?.utmContent) {
        return addParamsToURL(URL_LIST.DEEPL_COM_TRANSLATOR, [
          ["utm_source", "browser_extension"],
          ["utm_medium", "chrome"],
          ["utm_content", additionalData?.utmContent],
        ])
      } else {
        return URL_LIST.DEEPL_COM_TRANSLATOR
      }
    case URL_ACTIONS.deepl_write_home:
      if (additionalData?.utmContent) {
        return addParamsToURL(URL_LIST.DEEPL_COM_WRITE, [
          ["utm_source", "browser_extension"],
          ["utm_medium", "chrome"],
          ["utm_content", additionalData?.utmContent],
        ])
      } else {
        return URL_LIST.DEEPL_COM_WRITE
      }
    case URL_ACTIONS.deepl_translator_docs:
      if (additionalData?.utmContent) {
        return addParamsToURL(URL_LIST.DEEPL_COM_DOCTRANS, [
          ["utm_source", "browser_extension"],
          ["utm_medium", "chrome"],
          ["utm_content", additionalData?.utmContent],
        ])
      } else {
        return URL_LIST.DEEPL_COM_DOCTRANS
      }
    case URL_ACTIONS.deepl_terms_of_service_free:
      return URL_LIST.DEEPL_COM_TERMS_OF_SERVICE_FREE
    case URL_ACTIONS.shortcut_settings:
      return URL_LIST.SHORTCUT_SETTINGS
    case URL_ACTIONS.support_article:
      return URL_LIST.SUPPORT_ARTICLE
    // Onboarding links
    case URL_ACTIONS.external.whatsapp:
      return URL_LIST.EXTERNAL.WHATSAPP
    case URL_ACTIONS.external.twitter:
      return URL_LIST.EXTERNAL.TWITTER
    case URL_ACTIONS.external.youtube:
      return URL_LIST.EXTERNAL.YOUTUBE
    case URL_ACTIONS.external.facebook:
      return URL_LIST.EXTERNAL.FACEBOOK
    case URL_ACTIONS.external.github:
      return URL_LIST.EXTERNAL.GITHUB
    case URL_ACTIONS.external.discord:
      return URL_LIST.EXTERNAL.DISCORD
    case URL_ACTIONS.external.telegram:
      return URL_LIST.EXTERNAL.TELEGRAM
    case URL_ACTIONS.external.reddit:
      return URL_LIST.EXTERNAL.REDDIT
    case URL_ACTIONS.external.gmail:
      return URL_LIST.EXTERNAL.GMAIL
    case URL_ACTIONS.external.gdocs:
      return URL_LIST.EXTERNAL.GDOCS
    case URL_ACTIONS.external.gslides:
      return URL_LIST.EXTERNAL.GSLIDES
    case URL_ACTIONS.external.linkedIn:
      return URL_LIST.EXTERNAL.LINKEDIN
    case URL_ACTIONS.external.msOffice:
      return URL_LIST.EXTERNAL.MSOFFICE
    case URL_ACTIONS.deepl_support:
      return URL_LIST.DEEPL_SUPPORT
    case URL_ACTIONS.chrome_write_a_review:
      return URL_LIST.CHROME_WEBSTORE
    case URL_ACTIONS.edge_write_a_review:
      return URL_LIST.EDGE_WEBSTORE
    case URL_ACTIONS.gmail_feedback_survey:
      return URL_LIST.GMAIL_FEEDBACK_SURVEY
    case URL_ACTIONS.gslides_feedback_survey:
      return URL_LIST.GSLIDES_FEEDBACK_SURVEY
    case URL_ACTIONS.firefox_write_a_review:
      return URL_LIST.FIREFOX_WEBSTORE
    case URL_ACTIONS.firefox_privacy_policy:
      return URL_LIST.FIREFOX_PRIVACY_POLICY
    case URL_ACTIONS.firefox_feedback_survey:
      return URL_LIST.FIREFOX_FEEDBACK
    case URL_ACTIONS.firefox_shortcut_docs:
      return URL_LIST.FIREFOX_SHORTCUT_DOCS
    case URL_ACTIONS.deepl_create_account:
      if (additionalData) {
        let additionalParams = []
        if (additionalData.utmContent) {
          const utmParams = [
            ["utm_source", "browser_extension"],
            ["utm_medium", "chrome"],
            ["utm_content", additionalData.utmContent],
          ]
          additionalParams = [...utmParams]
        }
        // We submit a redirectURI to the backend, and are redirect back to the
        // extension on successful account creation
        if (additionalData.redirectToOnboarding) {
          if (!isFirefoxExtension()) {
            const redirectUrl = `${chrome.runtime.getURL("")}onboarding.html`
            additionalParams.push(["redirectUri", redirectUrl])
          }
        }
        return addParamsToURL(URL_LIST.DEEPL_CREATE_ACCOUNT, additionalParams)
      } else {
        return URL_LIST.DEEPL_CREATE_ACCOUNT
      }
    case URL_ACTIONS.customer_segmentation_survey_free:
      return URL_LIST.CUSTOMER_SEGMENTATION_SURVEY_FREE
    case URL_ACTIONS.customer_segmentation_survey_pro:
      return URL_LIST.CUSTOMER_SEGMENTATION_SURVEY_PRO
    case URL_ACTIONS.arabic_survey:
      return URL_LIST.ARABIC_SURVEY
    case URL_ACTIONS.ms_office_addins_ads:
      return URL_LIST.MSOFFICE_ADDINS_ADS
    default:
      return
  }
}

export function redirectToExternalURL(data) {
  const redirectURL = getExternalUrl(data)
  const { openInSameTab } = data

  if (!redirectURL) return

  const validatedUrl = validateURL(redirectURL)

  if (openInSameTab) {
    chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
      const currentTab = tabs[0]
      chrome.tabs.update(currentTab.id, { url: validatedUrl })
    })
  } else {
    chrome.tabs.create({
      url: validatedUrl,
      active: true,
    })
  }
}

function getShareURL({ sourceLang, targetLang, textToShare }) {
  if (!sourceLang || !targetLang || !textToShare) return ""
  const shareURL = new URL(URL_LIST.DEEPL_COM_TRANSLATOR)
  let pathname = `${sourceLang.toLowerCase()}/${targetLang.toLowerCase()}/${encodeURIComponent(
    textToShare
  )}`
  shareURL.hash = pathname
  return shareURL.href
}

function validateURL(url) {
  const newURL = new URL(url)
  return newURL.href
}

/**
 * Add one or more parameters to a URL
 * @param {string} url - The URL to append the parameters to
 * @param {Array} paramList  - list of parameter name and value pair tuples eg. [ ["name","value"]]
 * @returns {string} The URL with the parameters
 */
function addParamsToURL(url, paramList) {
  if (paramList.length === 0 || !url) return
  const newURL = new URL(url)
  paramList.forEach((param) => {
    param.length === 2 && newURL.searchParams.append(param[0], param[1])
  })
  return newURL.href
}
